class Draggable {

    constructor(rect) {
        this.rect = rect;

        this.sensitivity = 2;
        this.dragWait = 5;
        this.maxDrawCall = 4;

        this.oldX = 0;
        this.newX = 0;
        this.isDragging = false;
        this.reverse = true;
        this.canDrag = true;

        this.prevX = 0;
        this._bounding;
        this.init();
    }

    get _bounding(){
        return this.rect.getBoundingClientRect();
    }

    init() {
        this.rect.onmousedown = e => {
            this.mouseDown(e);
        }

        this.rect.onmousemove = e => {
            this.mouseMove(e);
        }

        this.rect.onmouseup = () => {
            this.mouseUp();
        }
    }

    mouseDown(e){
        this.isDragging = true;
        this.oldX = e.movementX;
        this.prevX = e.clientX - this._bounding.left;
    }

    mouseMove(e){
        
        if(!this.isDragging || !this.canDrag) return;
        
        this._RotateTest(e);
    }

    _RotateTest(e){
        
        const absMov = Math.abs(e.movementX);
        const currentX = e.clientX - this._bounding.left;
        const diff = currentX - this.prevX;
        
        if(Math.abs(diff) > this.sensitivity) {
            this.reverse = diff > 0;
            for (let i = 0; i < absMov % this.maxDrawCall; i++) {
                this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                    bubbles: false,
                    detail : this.reverse
                }));
            }
            
            window.requestAnimationFrame(timestamp => this.wait(timestamp));
            this.canDrag = false;
            this.prevX = currentX;
        }
        //this.prevX = currentX;
        //console.log(this.prevX);
        
    }

    _Rotate(e){
        const absMov = Math.abs(e.movementX);
        if(absMov >= 1){
            this.setDirection(e.movementX);
            for (let i = 0; i < absMov % this.maxDrawCall; i++) {
                this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                    bubbles: false,
                    detail : this.reverse
                }));
            }

            window.requestAnimationFrame(timestamp => this.wait(timestamp))
            this.canDrag = false;
        } 
    }

    mouseUp(){
        this.isDragging = false;
    }

    setDirection(deltaX){
        this.reverse = (deltaX > 0);
    }

    wait(timestamp){
        if(this.startWait == undefined){
            this.startWait = timestamp;
        }

        const elapsed = timestamp - this.startWait;

        if(elapsed > this.dragWait) {
            this.canDrag = true;
            this.startWait = timestamp;
            return;
        }

        window.requestAnimationFrame(timestamp => this.wait(timestamp));
    }
}


export {Draggable}